import React from 'react'
import CachedIcon from '@material-ui/icons/Cached'
import { ArrowDownward, ArrowUpward, Pause, PlayArrow } from '@material-ui/icons'

export default class Parameter extends React.Component {
  increment () {
    const newTime = this.props.value + 1
    this.props.setState({[this.props.connect]: newTime})
    this.updateTime(newTime)
  }

  decrement () {
    if (this.props.value - 1 > 0) {
      const newTime = this.props.value - 1
      this.props.setState({[this.props.connect]: newTime})
      this.updateTime(newTime)
    }
  }

  updateTime (newTime) {
    if (this.props.connect === 'sessionLength') {
      this.props.setState({time: newTime * 60})
    }
  }

  render () {
    return (
      <div className='inputs'>
        <p>{this.props.label}</p>
        <button onClick={this.increment.bind(this)}><ArrowUpward /></button>
        <p>{ this.props.value }</p>
        <button onClick={this.decrement.bind(this)}><ArrowDownward /></button>
      </div>
    )
  }
}
