import React from 'react'
import CachedIcon from '@material-ui/icons/Cached'
import { ArrowDownward, ArrowUpward, Pause, PlayArrow } from '@material-ui/icons'
import Parameter from './Parameter'

export default class App extends React.Component {
  constructor (props) {
    super()
    this.state = {
      sessionLength: 25,
      breakLength: 5,
      time: 25 * 60,
      timer: null
    }
  }

  startStop () {
    if (this.state.timer !== null) {
      window.clearInterval(this.state.timer)
      this.setState({ timer: null })
    }
    else {
      this.setState({
        timer: window.setInterval(() => {
          if (this.state.time === 0) {
            this.setState({ time: this.state.breakLength * 60 })
          } else {
            this.setState({ time: --this.state.time })
          }
        }, 1000)
      })
    }
  }

  formatTime (timeInS) {
    const m = parseInt(timeInS / 60)
    const s = (timeInS - m * 60)
    const fs = s > 9 ? s: '0' + s
    return m + ':' + fs
  }

  render () {
    return (
      <div className='app'>
        <Parameter
          label='Break Length'
          value={this.state.breakLength}
          connect='breakLength'
          setState={this.setState.bind(this)}
        />
        <Parameter
          label='Session Length'
          value={this.state.sessionLength}
          connect='sessionLength'
          setState={this.setState.bind(this)}
        />
        <p>{this.formatTime(this.state.time)}</p>
        <div className='countdown-buttons'>
          <button onClick={this.startStop.bind(this)}><PlayArrow /><Pause /></button>
          <button><CachedIcon /></button>
        </div>
      </div>
    )
  }
}
